.banner-explore-content {
    /* width: 1449px; */
    width: 100%;
    height: 539px;
    background-image: url('/images/banner-image.png');
    /* âœ… Starts with `/` */
    background-size: cover;
    gap: 100px;
}

.login-register {
    height: 39.33px;
    font-weight: 500;
    font-size: 14px;
    line-height: 35px;
}

.login-register a {
    color: #003366;
    text-decoration: none;
}

.logo {
    height: 30px;
}

.logo img {
    width: 100%;
    height: 100%;
}

.banner-explore-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.banner-explore-content-heading {
    width: 619px;
    height: 147px;
}

.banner-explore-content-heading .explore-heading {
    height: 80px;
    width: 619px;
}

.banner-explore-content-heading .explore-heading h1 {
    font-weight: 700;
    font-size: 64px;

    color: #ffffff;
    margin: 0;
    padding: 0;
}

.banner-explore-content-heading .explore-paragraph {
    height: 60px;
    width: 532px;
}

.banner-explore-content-heading .explore-paragraph p {
    font-weight: 400;
    font-size: 24px;
    line-height: 125%;
    text-align: center;
    color: #ffffff;
}

.banner-explore-content-filter {
    background-color: #ffffff;

    width: 1180px;
    height: 160px;
    border-end-end-radius: 16px;
    border-start-end-radius: 16px;
    border-bottom-left-radius: 16px;
}

.banner-explore-content-filter .explore-filter-head {}

.explore-filter-foot {
    padding: 32px;
}

.banner-explore-content-filter .explore-filter-head .fi-head-hotel {
    width: 142px;
    height: 48px;
    border-bottom: 3px solid #0560FB;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
}

.banner-explore-content-filter .explore-filter-foot .explore-filter-options {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
}

.explore-filter-options .explore-option {
    border: 1px solid #DCDCDC;
    width: 330px;
    height: 48px;
    border-radius: 10px;
    display: flex;
    padding: 11px 12px;
    gap: 8px;
}

.explore-filter-options .explore-option input {
    width: 198px;
    height: 22px;
    border: none;
}

.explore-filter-options .explore-option-btn button {
    width: 58px;
    height: 48px;
    background-color: #0560FB;
    border-radius: 10px;
    border: 1px solid #DCDCDC;
}

.book-services {
    background-color: #fff;
    padding: 0;
    margin-top: 50px;
}

.book-services-content {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

/* Banner Section */
.book-services-content-banner {
    position: relative;
    text-align: center;
    color: white;
}

.book-services-content-banner img {
    width: 100%;
    border-radius: 10px;
    object-fit: cover;
    max-height: 180px;
}

.book-services-content-banner h2 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 36px;
    font-weight: 700;
    color: #fff;
    text-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
}

.custom-input {
    padding: 10px 14px;
    border: 1px solid #ccc;
    border-radius: 8px;
    font-size: 14px;
    width: 260px;
    cursor: pointer;
    background-color: white;
}


/* Services Section */
.book-services-content-elements {
    margin-top: 40px;
    /* text-align: center; */
}

.book-services-content-elements h2 {
    font-size: 28px;
    font-weight: 600;
    margin-bottom: 30px;
    color: #333;
}

/* Cards Wrapper */
.hotel-book-services-wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
}

/* Individual Card */
.hotel-book-services-card {
    border: 1px solid #D1D5DB;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    background-color: #ffffff;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.06);
    border-radius: 12px;
    padding: 20px;
    flex: 1 1 calc(33.333% - 40px);
    min-width: 300px;
    max-width: 400px;
    box-sizing: border-box;
    transition: transform 0.3s ease;
}

.hotel-book-services-card:hover {
    transform: translateY(-5px);
}

/* Icon */
.hotel-book-icon {
    margin-right: 16px;
    flex-shrink: 0;
}

.hotel-book-icon img {
    width: 36px;
    height: 36px;
}

/* Text */
.hotel-book-text h3 {
    font-size: 18px;
    margin: 0 0 8px;
    color: #1a1a1a;
    font-weight: 600;
}

.hotel-book-text p {
    font-size: 14px;
    color: #555;
    margin: 0;
    line-height: 1.5;
}

/* Footer */
.footer {
    text-align: center;
    padding: 20px 0;
    font-size: 14px;
    color: #777;
    background-color: #1a1a1a;
    color: #ccc;
    margin-top: 40px;
}

.footer a {
    color: #ccc;
    text-decoration: underline;
}